*{
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

body{
  padding: 60px;
}

input{
  width: 100%;
  font-size:1.2rem;
  color:grey;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
  max-width: 350px;

}

.card p {
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
  max-width: 100%;
}

.animate__animated.animate__flash {
  --animate-duration: 2s;
}